import React, { memo, useCallback, useState } from "react";
//reactpdf
import PDF from "react-pdf-js";
import { Button } from "antd";
import pdfs from "./-202100130221-3.pdf"; // pdf

export default memo(() => {
  const [pages, setPages] = useState({ page: 1, allPages: 1 });

  const getAllPages = useCallback(
    (pageNums) => {
      setPages((prev) => ({ ...prev, allPages: pageNums }));
    },
    [pages]
  );

  const nextPage = useCallback(
    (type) => {
      let currentPage = pages["page"];
      if (type == "next") {
        // 
        if (currentPage == pages["allPages"]) {
          alert("");
        } else {
          setPages((prev) => ({ ...prev, page: currentPage + 1 }));
        }
      } else {
        // 
        if (currentPage == 1) {
          alert("");
        } else {
          setPages((prev) => ({ ...prev, page: currentPage - 1 }));
        }
      }
    },
    [pages]
  );

  return (
    <div>
    <Button type="primary" onClick={() => nextPage("up")}></Button>
        <span>
          {pages["page"]}/{pages["allPages"]}
        </span>
        <Button type="primary" onClick={() => nextPage("next")}></Button>
      <PDF
          file={pdfs} //
          onDocumentComplete={getAllPages}
          page={pages["page"]} //
        /> 
    </div>
  );
});

